"use client";
import React, { useEffect, useState } from "react";
import Link from "next/link";

function Navbar() {
  const [hideNav, setHideNav] = useState(false);
  const [lastScrollY, setLastScrollY] = useState(0);

  useEffect(() => {
    const handleScroll = () => {
      const currentY = window.scrollY;

      if (currentY > lastScrollY && currentY > 50) {
        setHideNav(true);
      } else {
        setHideNav(false);
      }

      setLastScrollY(currentY);
    };

    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, [lastScrollY]);

  return (
    <header
      className={`fixed top-0 left-0 w-full z-50 bg-transparent text-white p-4 transition-transform duration-300 ${
        hideNav ? "-translate-y-full" : "translate-y-0"
      }`}
    >
      <nav className="flex justify-between items-center w-full max-w-6xl mx-auto">
        <h1 className="text-xl font-bold">Study Buddy</h1>
        <div className="space-x-4 flex">
          <Link href="/signup">
            <div className="bg-white text-blue-600 font-semibold px-4 py-2 rounded-md hover:bg-gray-100 transition cursor-pointer">
              Sign Up
            </div>
          </Link>
          <Link href="/signin">
            <div className="border border-white text-white px-4 py-2 rounded-md hover:bg-white hover:text-blue-600 transition cursor-pointer">
              Sign In
            </div>
          </Link>
        </div>
      </nav>
    </header>
  );
}

export default Navbar;
